/* Carousel.css */

.carousel {
  overflow: hidden; /* 넘치는 컨텐츠를 숨기기 위해 */
  position: relative;
}

.carousel-slide {
  display: flex; /* Flex 컨테이너로 설정하여 컨텐츠를 가로로 배열합니다 */
  transition: transform 0.5s ease; /* 슬라이드 애니메이션을 적용합니다 */
}

.carousel-slide > div {
  flex: 0 0 100%; /* 각 슬라이드를 가로로 정렬합니다 */
}

.pagination {
  display: flex;
  justify-content: center;
}

.pagination span {
  display: inline-block;
  width: 10px;
  height: 4px;
  background-color: gray;
  margin: 0 5px;
  cursor: pointer;
  transition: all .2s ease;
}

.pagination span.active {
  background-color: white;
  width: 40px; 
}

.pagination span.deactive {
  background-color: rgba(255, 255, 255, 0.5);
  width: 20px;
}

.carouseImagelItem {
  background: none;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.carouseImagelItem > div {
  z-index: 2;
  width: 100%;
  height: 100%;
  position: relative;
  background-size: fit;
  background-position: center;
  background-repeat: no-repeat;
  justify-content: center;
  align-items: center;
  display: flex;
}

.carouseImagelItem > div > div {
  height: 100%;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(0, 0, 0, 0.5);
}

.carouseImagelItem > div > div > div {
  padding-top: 144px;
  padding-bottom: 72px;
  padding-left: 24px;
  padding-right: 24px;
  display: flex;
  justify-content: center;
  align-items: center;
  width: min(840px, 100%);
}


.adaptive-height {
  height: 480px;
}

.adaptive-margin {
  margin-top: 455px;
}

@media ( max-width: 720px ) {
  .adaptive-height {
    height: 360px;
  }
  .adaptive-margin {
    margin-top: 325px;
  }
  
}