
.tag {
  background-color: #f5f5f5;
  color: #7a7a7a;
  height: 24px;
  border-radius: 24px;
  transition: all .3s ease;
  align-items: center;
  text-align: center;
  justify-content: center;
  padding-inline: 10px;
}

@media (prefers-color-scheme: dark) {
  .tag {
    background-color: #0b0b0b;
    color: #969696;
  }
}